.login {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100vh;
  width: 100%;
  color: hsl(224, 47%, 43%);
}

.login > div > img {
  object-fit: contain;
  margin-left: 12%;
  height: 250px;
}

.login > div > button {
  width: 100px;
  color: white;
  margin: 10px;
  background-color: hsl(224, 47%, 43%);
}

.login > div > button:hover {
  color: hsl(224, 47%, 43%);
  background-color: white;
}

.signIn_default {
  display: none;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
}
.signIn {
  position: absolute;
  background: hsl(224, 47%, 43%);
  border: 1px solid rgba(190, 190, 190, 0.1);
  border-radius: 5px;
  /* flex: .25; */
  height: 50%;
  width: 80%;
  top: 10%;
  left: 10%;
  overflow: hidden;
  transform: translateX(0%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.signUp_default {
  display: none;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
}
.signUp {
  position: absolute;
  background: hsl(224, 47%, 43%);
  border: 1px solid rgba(190, 190, 190, 0.1);
  border-radius: 5px;
  /* flex: .25; */
  height: 50%;
  width: 80%;
  top: 10%;
  left: 10%;
  overflow: hidden;
  transform: translateX(0%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
