.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #04aa6d;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #04aa6d;
  cursor: pointer;
}

ul {
  display: block;
  margin-left: -10px;
}

ul li {
  display: block;
  position: relative;
  color: #303ca7;
}

ul li:not(:last-child) {
  margin-bottom: 16px;
}

ul li:before {
  content: '';
  position: absolute;
  top: 1.2em;
  left: -30px;
  margin-top: -0.9em;
  background: #303ca7;
  height: 12px;
  width: 12px;
  border-radius: 50%;
}
ul ul li::before {
  background: darkorchid;
}
